import { StateCreator } from 'zustand';

export interface WeatherSlice {
  city: string;
  weatherInfo: any;
  setWeatherInfo: (weatherInfo: any) => void;
};

export const createWeatherSlice: StateCreator<WeatherSlice> = (set) => ({
  city: '杭州',
  weatherInfo: null,
  setWeatherInfo: (weatherInfo) => (set({ weatherInfo })),
});
