import React, { useEffect, useState } from 'react';
import classnames from 'classnames';
import {
  EnvironmentOutlined,
  FireOutlined,
  CompassOutlined,
} from '@ant-design/icons';
import Copyright from '@/components/Copyright';
import commonStyles from '../common.less';
import styles from './index.less';
import { useWeatherStore } from '@/stores';
import { dateFormat } from '@/utils/tool';
import G2Line from '@/module/components/G2Line';

const DefalutPanel = () => {
  const city = useWeatherStore((state) => state.city);
  const weatherInfo = useWeatherStore((state) => state.weatherInfo);
  const [lineDateBy24Hour, setLineDateBy24Hour] = useState([]);
  const [lineDateByWeek, setLineDateByWeek] = useState([]);
  const [curDayInfo, setCurDayInfo] = useState<any>({});
  const [nowInfo, setNowInfo] = useState<any>({});

  useEffect(() => {
    if (weatherInfo?.data) {
      setLineDateByWeek((weatherInfo?.data?.daily || []).flatMap((i: any) => ([
        { time: i.fxDate, value: +i.tempMin, type: 'tempMin' },
        { time: i.fxDate, value: +i.tempMax, type: 'tempMax' },
      ])));
      setLineDateBy24Hour((weatherInfo?.data?.hourly || []).map((i: any) => (
        { time: i.fxTime, value: +i.temp }
      )));
      setCurDayInfo(weatherInfo?.data?.daily[0]);
      setNowInfo(weatherInfo?.data?.now);
    }
  }, [weatherInfo?.data]);

  return (
    <div className={styles.container}>
      <div className={classnames(styles.bgCover, commonStyles.yin_d)} />
      {weatherInfo?.data?.updateTime && (
        <div className={styles.panel}>
          <div className={classnames(styles.topPanel, styles.flex1)}>
            <div className={styles.nowPanel}>
              <div className={styles.weatherNowtmp}>
                <div className={styles.nowTmp}>{`${nowInfo?.temp || 0}°`}</div>
                <div className={styles.info}>
                  <div>
                    <EnvironmentOutlined />
                    <span>{city} 发布于: {dateFormat(weatherInfo?.data?.updateTime, 'MM-dd hh:mm') || '-'}</span>
                  </div>
                  <div>
                    <FireOutlined />
                    <span>{`${nowInfo?.category || '-'} / ${nowInfo?.aqi || 0}`}</span>
                  </div>
                  <div>
                    <CompassOutlined />
                    <span>{`${nowInfo?.text || ''} ${nowInfo?.windDir || ''} ${nowInfo?.windScale || 0}级`}</span>
                  </div>
                </div>
              </div>
            </div>
            <div className={styles.weatherInfo}>
              <div><h5>温度</h5><span>{`${curDayInfo?.tempMin || 0}°~${curDayInfo?.tempMax || 0}°`}</span></div>
              <div><h5>湿度</h5><span>{`${curDayInfo?.humidity || 0}%`}</span></div>
              <div><h5>降水</h5><span>{`${curDayInfo?.precip || 0}mm`}</span></div>
              <div><h5>气压</h5><span>{`${curDayInfo?.pressure || 0}hPa`}</span></div>
              <div><h5>日出</h5><span>{curDayInfo?.sunrise || '-'}</span></div>
              <div><h5>日落</h5><span>{curDayInfo?.sunset || '-'}</span></div>
            </div>
          </div>
          <div className={classnames(styles.listPanel, styles.flex2, styles.hoursPanel)}>
            <h3>24小时趋势图</h3>
            <ul>
              {(weatherInfo?.data?.hourly || []).map((item: any, index: any) => (
                <li key={`${index}`}>
                  <div className={styles.text}>
                    {`${dateFormat(item.fxTime, 'h') || 0}时`}
                  </div>
                  <div className={styles.text}>{item.text || '-'}</div>
                  <div className={styles.flex1} />
                </li>
              ))}
              <div className={styles.linePanel}>
                <G2Line updateTime={weatherInfo?.data?.updateTime} data={lineDateBy24Hour} />
              </div>
            </ul>
          </div>
          <div className={classnames(styles.listPanel, styles.flex4, styles.weekPanel)}>
            <h3>7日趋势图</h3>
            <ul>
              {(weatherInfo?.data?.daily || []).map((item: any, index: any) => (
                <li key={`${index}`}>
                  <div className={styles.text}>{dateFormat(item.fxDate, 'MM-dd') || '-'}</div>
                  <div className={styles.text}>{item.textDay || '-'}</div>
                  <div className={styles.flex1} />
                  <div className={styles.text}>{`${item.windScaleDay || 0}级`}</div>
                </li>
              ))}
              <div className={styles.linePanel}>
                <G2Line updateTime={weatherInfo?.data?.updateTime} data={lineDateByWeek} />
              </div>
            </ul>
          </div>
        </div>
      )}
      <div className={styles.copyright}>
        <Copyright />
      </div>
    </div>
  );
};

export default DefalutPanel;
