import React, { useEffect, useState } from 'react';
import OldDefalutPanel from '@/module/view/OldDefalutPanel';
import DefalutPanel from '@/module/view/DefalutPanel';
import MiniPanel from '@/module/view/MiniPanel';
import Popup from '@/components/Popup';
import { useWeatherStore } from '@/stores';
import * as weatherController from '@/module/controller';
import styles from './app.less';

function App({ view, entryUrl }: any) {
  const setWeatherInfo = useWeatherStore((state) => state.setWeatherInfo);

  const getWeatherInfo = async () => {
    const result = await weatherController.getWeatherInfo();
    setWeatherInfo(result);
  };

  useEffect(() => {
    getWeatherInfo();
  }, []);

  const [date, setDate] = useState(new Date());
  useEffect(() => {
    const tick = () => {
      setDate(new Date());
      getWeatherInfo();
    };
    const timeId = setTimeout(tick, 900000);
    return () => clearTimeout(timeId);
  }, [date]);

  return (
    (view === 'showroom') ? (
      <Popup
        width={1024}
        panel={() => <DefalutPanel />}
        link={entryUrl}
        needTrack
      >
        <div className={styles.main}><MiniPanel /></div>
      </Popup>
    ) : <OldDefalutPanel />
  );
}

export default App;
