import React, { useEffect, useState } from 'react';
import classnames from 'classnames';
import {
  EnvironmentOutlined,
  FireOutlined,
  CompassOutlined,
} from '@ant-design/icons';
import Layout from '@/components/LayoutNew';
import Copyright from '@/components/Copyright';
import styles from './index.less';
import { useWeatherStore } from '@/stores';
import { dateFormat } from '@/utils/tool';
import G2Line from '@/module/components/G2Line';

const WeatherAssets = {
  'xiayu': { // 下雨
    color: 'rgba(67,106,102,1)',
    image: 'https://search-operate.cdn.bcebos.com/9669cac22a5833622982ecfdf38f4694.png',
    video: 'https://search-operate.cdn.bcebos.com/5b8fa41e5c92ea8793a99c5081a7a9d8.mp4',
  },
  'yin': { // 阴
    color: 'rgba(109,125,150,1)',
    image: 'https://search-operate.cdn.bcebos.com/1e53eb7bbd8fe233d4deae60abf5ce01.png',
    video: 'https://search-operate.cdn.bcebos.com/98883df887a55c54f90befe517b0f6b8.mp4',
  },
  'qing': { // 晴
    color: 'rgba(92,162,221,1)',
    image: 'https://search-operate.cdn.bcebos.com/cd614b12ef78f515e478c2494086b370.png',
    video: 'https://search-operate.cdn.bcebos.com/3ad889fd821bc79439b29e7600dad777.mp4',
  },
  'qingwan': { // 晴 晚
    color: 'rgba(74,82,130,1)',
    image: 'https://search-operate.cdn.bcebos.com/bee86a441480c0aa9c0adff674f8f715.png',
    video: 'https://search-operate.cdn.bcebos.com/1de63c2303ac334218c0ad19494f6f12.mp4',
  },
  'duoyun': { // 多云
    color: 'rgba(94,165,224,1)',
    image: 'https://search-operate.cdn.bcebos.com/cf23aa483c94da0335d7a42b2682f373.png',
    video: 'https://search-operate.cdn.bcebos.com/23ef1fc7fbd5829ac08f5656a4bfc9ba.mp4',
  },
  'duoyunwan': { // 多云 晚
    color: 'rgba(74,82,130,1)',
    image: 'https://search-operate.cdn.bcebos.com/dcb9d0d77671f3b2e2738cc394f9ee55.png',
    video: 'https://search-operate.cdn.bcebos.com/763f9ffc102867da586d93ac171adb01.mp4',
  },
};

const DefalutPanel = () => {
  const city = useWeatherStore((state) => state.city);
  const weatherInfo = useWeatherStore((state) => state.weatherInfo);
  const [lineDateBy24Hour, setLineDateBy24Hour] = useState([]);
  const [lineDateByWeek, setLineDateByWeek] = useState([]);
  const [curDayInfo, setCurDayInfo] = useState<any>({});
  const [nowInfo, setNowInfo] = useState<any>({});

  useEffect(() => {
    if (weatherInfo?.data) {
      setLineDateByWeek((weatherInfo?.data?.daily || []).flatMap((i: any) => ([
        { time: i.fxDate, value: +i.tempMin, type: 'tempMin' },
        { time: i.fxDate, value: +i.tempMax, type: 'tempMax' },
      ])));
      setLineDateBy24Hour((weatherInfo?.data?.hourly || []).map((i: any) => (
        { time: i.fxTime, value: +i.temp }
      )));
      setCurDayInfo(weatherInfo?.data?.daily[0]);
      setNowInfo(weatherInfo?.data?.now);
    }
  }, [weatherInfo?.data]);

  const type = 'duoyunwan';

  const RenderBgPanel = ({ needVideo = false }) => (
    <div className={styles.bgPanel} style={{ backgroundColor: WeatherAssets[type].color }}>
      <div className={styles.image} style={{ backgroundImage: `url(${WeatherAssets[type].image})` }}></div>
      {needVideo && (
        <video className={styles.video} src={WeatherAssets[type].video} poster={WeatherAssets[type].image} muted autoPlay auto-rotate="false" playsInline crossOrigin="anonymous" loop />
      )}
    </div>
  );
  const RenderWeatherNowTmp = () => <div className={styles.nowTmp}>{`${nowInfo?.temp || 0}°`}</div>;
  const RenderWeatherNowInfo = () => (
    <div className={styles.info}>
      <div>
        <EnvironmentOutlined />
        <span>{city} 发布于: {dateFormat(weatherInfo?.data?.updateTime, 'MM-dd hh:mm') || '-'}</span>
      </div>
      <div>
        <FireOutlined />
        <span>{`${nowInfo?.category || '-'} / ${nowInfo?.aqi || 0}`}</span>
      </div>
      <div>
        <CompassOutlined />
        <span>{`${nowInfo?.text || ''} ${nowInfo?.windDir || ''} ${nowInfo?.windScale || 0}级`}</span>
      </div>
    </div>
  );
  const RenderWeatherDayInfo = () => (
    <div className={styles.weatherInfo}>
      <div><h5>温度</h5><span>{`${curDayInfo?.tempMin || 0}°~${curDayInfo?.tempMax || 0}°`}</span></div>
      <div><h5>湿度</h5><span>{`${curDayInfo?.humidity || 0}%`}</span></div>
      <div><h5>降水</h5><span>{`${curDayInfo?.precip || 0}mm`}</span></div>
      <div><h5>气压</h5><span>{`${curDayInfo?.pressure || 0}hPa`}</span></div>
      <div><h5>日出</h5><span>{curDayInfo?.sunrise || '-'}</span></div>
      <div><h5>日落</h5><span>{curDayInfo?.sunset || '-'}</span></div>
    </div>
  );
  const RenderHourlyPanel = () => (
    <div className={classnames(styles.listPanel, styles.hoursPanel)}>
      <h3>24小时趋势图</h3>
      <ul>
        {(weatherInfo?.data?.hourly || []).map((item: any, index: any) => (
          <li key={`${index}`}>
            <div />
            <div>
              <div className={styles.text}>
                {`${dateFormat(item.fxTime, 'h') || 0}时`}
              </div>
              <div className={styles.text}>{item.text || '-'}</div>
            </div>
          </li>
        ))}
        <div className={styles.linePanel}>
          <G2Line updateTime={weatherInfo?.data?.updateTime} data={lineDateBy24Hour} />
        </div>
      </ul>
    </div>
  );
  const RenderDailyPanel = () => (
    <div className={classnames(styles.listPanel, styles.weekPanel)}>
      <h3>7日趋势图</h3>
      <ul>
        {(weatherInfo?.data?.daily || []).map((item: any, index: any) => (
          <li key={`${index}`}>
            <div>
              <div className={styles.text}>{dateFormat(item.fxDate, 'MM-dd') || '-'}</div>
              <div className={styles.text}>{item.textDay || '-'}</div>
            </div>
            <div className={styles.flex1} />
            <div className={styles.text}>{`${item.windScaleDay || 0}级`}</div>
          </li>
        ))}
        <div className={styles.linePanel}>
          <G2Line updateTime={weatherInfo?.data?.updateTime} data={lineDateByWeek} />
        </div>
      </ul>
    </div>
  );

  return (
    <Layout
      main={(
        <div className={styles.mainContainer}>
          <div className={styles.bgCover}>
            <RenderBgPanel needVideo />
            {/* <div className={styles.mask} /> */}
          </div>
          <div className={styles.panel}>
            <RenderHourlyPanel />
            <RenderDailyPanel />
          </div>
        </div>
      )}
      mini={(
        <div className={styles.miniContainer}>
          <div className={styles.header}>
          </div>
          <div className={styles.floor}>
            <RenderWeatherNowTmp />
            <RenderWeatherNowInfo />
            <RenderWeatherDayInfo />
          </div>
          <div className={styles.copyright}>
            <Copyright />
          </div>
        </div>
      )}
      backdrop={(
        <div className={styles.backdrop}>
          <RenderBgPanel />
          <div className={styles.mask} />
        </div>
      )}
    />
  );
};

export default DefalutPanel;
