import React, { useEffect, useState } from 'react';
import classnames from 'classnames';
import { useWeatherStore } from '@/stores';
import styles from './index.less';
import commonStyles from '../common.less';

const MiniPanel = () => {
  const city = useWeatherStore((state) => state.city);
  const weatherInfo = useWeatherStore((state) => state.weatherInfo);

  // const curDayInfo = weatherInfo?.data?.daily[0];
  // const nowInfo = weatherInfo?.data?.now;
  // const { tmp_max, tmp_min } = weatherInfo?.daily_forecast[0];
  // const [curDayInfo, setCurDayInfo] = useState<any>({});
  const [nowInfo, setNowInfo] = useState<any>({});

  useEffect(() => {
    if (weatherInfo?.data) {
      // setCurDayInfo(weatherInfo?.data?.daily[0]);
      setNowInfo(weatherInfo?.data?.now);
    }
  }, [weatherInfo?.data]);

  return nowInfo?.temp ? (
    <div className={styles.container}>
      {/* <div className={classnames(styles.bgCover, commonStyles.yin_d)} /> */}
      <div className={styles.panel}>
        <div className={styles.left}>
          <div className={styles.icon}><i className={`qi-${nowInfo?.icon}-fill`} /></div>
        </div>
        <div className={styles.right}>
          <div className={styles.title}>
            {`${nowInfo?.text} ${nowInfo?.temp}°`}
          </div>
          <div className={styles.info}>
            {`${city}天气`}
          </div>
        </div>
      </div>
    </div>
  ) : null;
};

export default MiniPanel;
