import storage from '@/utils/storage';
import { dateFormat } from '@/utils/tool';
import { requestCodeTextMap } from '@/const';

// 杭州 不打算切换 反正是自用 那就固定成自己常用的就好 间隔
const location = 101210101;
const key = 'e87a-76a7-c419-432d-8175-73ef-8997-ca1e'.replace(/-/img, '');
const API_HOST = 'https://devapi.qweather.com';
const query = `location=${location}&key=${key}`;
const API = {
  // 获取当前天气 /v7/weather/now?{查询参数}
  getNow: `${API_HOST}/v7/weather/now?${query}`,
  // 获取24小时预报 /v7/weather/24h?{查询参数}
  get24h: `${API_HOST}/v7/weather/24h?${query}`,
  // 获取7日预报 /v7/weather/7d?{查询参数}
  get7d: `${API_HOST}/v7/weather/7d?${query}`,
  // 实时空气质量 /v7/air/now?{查询参数}
  getAir: `${API_HOST}/v7/air/now?${query}`,
};

const request = (url: string) => fetch(url)
  .then(response => response.text())
  .then(text => JSON.parse(text))
  .then(result => ({ success: result.code === '200', data: result, message: requestCodeTextMap[result.code] || '' }))
  .catch(error => ({ success: false, data: null, message: error?.message || 'Failed to fetch' }));

export const getWeatherInfo = async () => {
  const microAppName = require('../../../package.json').name;
  let resultData = storage.get(microAppName);

  if (
    resultData
    && resultData?.updateTimestamp
    && (Date.now() - resultData.updateTimestamp <= 3600000)
  ) {
    return {
      success:true,
      message: '',
      data: resultData,
    };
  }

  // TODO 如果与缓存的时间超过1小时 则更新数据
  const[resultNow, result24h, result7d, resultAir] = await Promise.all([
    request(API.getNow),
    request(API.get24h),
    request(API.get7d),
    request(API.getAir),
  ]);
  resultData = {
    ...resultNow.data,
    ...result24h.data,
    ...result7d.data,
    ...resultAir.data,
    now: {
      ...resultNow.data.now,
      ...resultAir.data.now,
    },
    updateTimestamp: +dateFormat(resultNow?.data?.updateTime, 'timestamp'),
  };
  if (resultNow.success && result24h.success && result7d.success && resultAir.success) {
    storage.set(microAppName, resultData);
  }
  return {
    success: resultNow.success && result24h.success && result7d.success && resultAir.success,
    message: resultNow.message || result24h.message || result7d.message || resultAir.message,
    data: resultData,
  };
};
