import React, { useRef, useEffect, useState } from 'react';
import { Chart } from '@antv/g2';

interface IProps {
  updateTime?: number;
  className?: string;
  data?: any[];
};

const G2Line = (props: IProps) => {
  const chartRef = useRef<HTMLDivElement>(null);
  const chart = useRef<any>(null);
  const [lastUpdateTime, setLastUpdateTime] = useState(0);

  useEffect(() => {
    if (chartRef.current) {
      // 创建图表实例
      chart.current = new Chart({
        container: chartRef.current,
        autoFit: true,
        margin: 0,
        paddingTop: 40,
        paddingBottom: 20,
      });
    }

    return () => {
      // 组件卸载时销毁图表实例
      if (chart.current) {
        chart.current.destroy();
      }
    };
  }, []); // 仅在组件挂载时执行

  useEffect(() => {
    if (chart.current && props.data && props.data.length && lastUpdateTime !== props?.updateTime) {
      chart.current.clear();

      // 更新图表配置
      chart.current
        .data(props.data)
        .encode('x', 'time')
        .encode('y', 'value')
        .encode('color', 'type')
        .scale('x', { range: [0, 1] })
        .scale('y', { nice: true })
        .axis('x', { tick: false, label: false, title: false })
        .axis('y', { tick: false, label: false, title: false })
        .legend('color', false);

        chart.current
          .line()
          .label({
            text: 'value',
            style: {
              dy: -20,
              textBaseline: 'top',
              textAlign: 'center',
              fill: '#fff',
            },
            formatter: (d: number) => d + '°'
          })
          .style('stroke', 'white')
          .tooltip(false);

        chart.current
          .point()
          .encode('shape', 'point')
          .style('fill', 'white')
          .tooltip(false);

      // 渲染图表
      chart.current.render();
      setLastUpdateTime(props.updateTime || 0);
    }
  }, [props?.updateTime, props?.data]);

  return (
    <div className={props.className} ref={chartRef} />
  );
};

export default G2Line;
